.feedback-form {
  max-width: 480px;
  background: none;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #fff;
  border-bottom: 2px solid #fff;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #fff;
  background: none;
  border-color: #fff;
  border-radius: 40px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  background: none;
  border: none;
  outline: none;
  padding: 8px;
}

textarea {
  resize: none;
}

.feedback-form button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

